//Загальні стилі

* {
  box-sizing: border-box;
  vertical-align: top;
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  outline: none;
  border: none;
  transition: all .2s;
}

body {
  min-width: 320px;
  margin: 0 auto;
  position: relative;
}

.title_2 {
  font: 28px/32px 'PT Sans';
  color: @three-color;
  font-weight: 400;
  text-transform: uppercase;


